import service from '../../service.js';
export default {
	data() {
		return {
			unfreeze: '', //可申请提现 
			freeze: '', //冻结 
			paid: '', //已合计提现金额
			listsss: [],
			unfreeze: '', //可申请提现 ,
			freeze: '', //冻结,
			paid: '', //已合计提现金额
			loadshow: true,
			page: 0,
			pages: 1

		}
	},
	methods: {
		jump(url) {
			uni.switchTab({
				url: url
			})
		},
		jum(url) {
			uni.navigateTo({
				url: url
			})
		},
		add() {
			this.loadData();
		},
		loadData() {
			service.loadthepage({
				url: service.api.record.integralDetail,
				data: {
					limit: 15,
					page: this.page + 1,
				},
				method: 'GET',
				direct: true,
				that: this
			});

		}
	},
	onShow() {
		service.auth(this, service.api.record.index, 'GET', '', function(self, res) {
			console.log(res);
			self.unfreeze = res.data.unfreeze;
			self.freeze = res.data.freeze;
			self.paid = res.data.paid;
		});

		// service.auth(this, service.api.record.integralDetail, 'GET', '', function(self, res) {
		// 	console.log(res);

		// });
		this.loadData();
	}
}
